import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { CustGroupListBean } from './../../bean/custom-group.bean';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule, AccordionModule } from 'primeng/primeng';
import { Observable } from 'rxjs/Rx';
import * as _ from 'lodash';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';



@Component({
  selector: 'group-client-check',
  templateUrl: './group-client-check.component.html',
  styleUrls: ['./group-client-check.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class GroupClientCheckComponent implements OnInit, OnChanges {

  // 入参
  groupId: any = '';
  listType: any = '';
  //公共
  tableMesg: any = tableMessage;
  title1: string = '客户名单查询';
  title2: string = '客户名单';
  // bean
  custGroupListBean: CustGroupListBean = new CustGroupListBean();//客户群名单
  msgs: Message[] = [];
  // 传参
  //  queryParams: any = {};//客户名单参数
  params: any = {};//客户名单参数
  cId: any = [];
  cName: any = [];
  list: any;//列表
  session_info: any;

  choose_arr: any = [];
  choose_json: any = [];


  i: any = 1;
  //duanxin
  // 中间量
  evaluateFlag: boolean = false;

  addDisplay: boolean = false;
  successNum: any;
  faileNum: any;
  failInfoList: any;

  // 组件
  detailDisplay: boolean = false;
  detailData: any = [];
  // 控件
  selectedValues: any = [];//单选数组
  allVal: any = true;//全选值
  selectedAllVal: any;//全选数组
  flag: boolean = false;//中间量

  first: any = 0;
  display: boolean = false;

  // 码值
  code:any;
  relType: any;// = CustomGroupRelType;//关联关系
  idenType: any;// = CustomGroupIdenType;//证件类型
  constructor(
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public Commfunc: Commfunc,
    public routerInfo: ActivatedRoute,

  ) { 
    this.codeValues() //调用方法，获取全部码值
//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
this.relType = this.code['CustomGroupRelType']; 
this.idenType = this.code['CustomGroupIdenType'];
  }

//码值
codeValues() {
  this.Commfunc.codeValue();
  let ret = this.Commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  ngOnInit() {
    this.preSet();
    this.session_info = JSON.parse(sessionStorage.getItem("chName"));
    this.selectCustGroupList();//客户群名单查询          

  }
  ngOnChanges(changes) {

  }
  preSet() {
    this.custGroupListBean.listType = '1';//名单类型    

    // 传参
    if (this.routerInfo.snapshot.params.groupId) {
      this.groupId = this.routerInfo.snapshot.params.groupId;
    }
    if (this.routerInfo.snapshot.params.listType) {
      this.listType = this.routerInfo.snapshot.params.listType;
    }
    // 初始化
    this.custGroupListBean.pageSize = 999;//每页条数
    this.custGroupListBean.pageNum = 1;//当前页数
    if (this.groupId) {
      this.custGroupListBean.groupId = this.groupId;//群号
    }
    if (this.listType) {
      this.custGroupListBean.listType = this.listType;//群名称
    }
  }
  // 判断是否临时客户
  isQl(col) {
    if (col.custNo.search(/^QL/)) {
      return true;
    }
    return false;
  }
  selectCustGroupList() {
    this.custGroupListBean["statusId"] = '1';
    //客户群名单查询
    this.httpService.selectCustGroupList(this.custGroupListBean).subscribe(data => {
      if (data.returnCode.code) {
        this.custGroupListBean = data;
        this.custGroupListBean.custList = data.custList;
        this.list = data.custList;
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }



}
